@use "../abstracts/mixins";
@import url('https://fonts.googleapis.com/css2?family=Red+Hat+Text:wght@700&display=swap');

.main-heading {
    font-size: 20px;
    @include mixins.small-412 {
        font-size: 15px;
    }

    @include mixins.medium-992 {
        font-size: 19px;
    }

    @include mixins.big-1400 {
        font-size: 20px;
    }
    letter-spacing: 3px;
    text-transform: uppercase;
    line-height: 1.3;
}


.countdown {

    &__time {
        font-size: 33px;
        @include mixins.medium-992 {
            font-size: 37px;
        }

        @include mixins.big-1400 {
            font-size: 45px;
        }
    }

    &__text {
        font-size: 8px;
        @include mixins.small-600 {
            font-size: 10px;
        }

        @include mixins.big-1400 {
            font-size: 12px;
        }
        text-transform: uppercase;
        letter-spacing: 3px;
    }
}



