@use "../abstracts/mixins";

.countdown {
    display: flex;
    justify-content: center;

    &__box + &__box {
        margin-left: 18px;
    }

    &__box {
        flex-basis: 75px;
        
       @include mixins.medium-992 {
           flex-basis: 80px;
       }
       @include mixins.big-1400 {
           flex-basis: 90px;
       }
    }

   
    &__time {
        background: hsl(236, 21%, 26%);
        color: hsl(345, 95%, 68%);
        padding: 15px;
        border-radius: 5px;
        box-shadow: 0 5px 0 hsl(234, 17%, 12%);
        text-align: center;
    }

    &__text {
        text-align: center;
        color: hsl(237, 18%, 59%);
        margin-top: 10px;;
    }   
}
